import React from "react";
import styled from "styled-components";
import HoverBox from "../layout/hoverbox";
import { LayoutContainer, LayoutItem, LayoutItemWrapper, LayoutRow } from '../layout/layout';
import { ButtonLink } from '../base/links';
import { Heading, Text } from '../base/typography';
import SvgWrapper from '../base/svgwrapper';
import ContactBoxItem from "./contactbox";
import Obfuscate from 'react-obfuscate';

import SVGContactMail from '../../images/svg/contact-mail.svg';
import SVGContactPhone from '../../images/svg/contact-phone.svg';
import SVGContactCoffee from '../../images/svg/contact-coffee.svg'

const ContactHero = () => {
    return (
    <LayoutContainer>
        <LayoutRow center>
            <ContactBoxPhone />
            <ContactBoxMail />
        </LayoutRow>
    </LayoutContainer>
    )
};

const ContactBoxIconPhone = styled(SVGContactPhone)`
    @keyframes ring {
        from {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
        }

        10%,
        20% {
        -webkit-transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
        transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
        }

        30%,
        50%,
        70%,
        90% {
        -webkit-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
        transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
        }

        40%,
        60%,
        80% {
        -webkit-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
        transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
        }

        to {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
        }
    }

    ${ContactBoxItem}:hover & {
        animation: ring 1s;
    }
`;

const ContactBoxIconMail = styled(SVGContactMail)`
    overflow: visible;

    .envelope {
        fill: var(--color-neutral-0);
        stroke: var(--color-neutral-0);
        stroke-width: var(--spacing-xs);
    }
    
    .envelop__lid {
        transition: all var(--transition-fast);
    }
    
    .envelop__inner-lid {
        transition: all var(--transition-fast);
    }
    
    ${ContactBoxItem}:hover & .envelop__lid {
        fill: var(--color-neutral-9);
        transform: scaleY(-1.0);
    }
    
    ${ContactBoxItem}:hover & .envelop__inner-lid {
        transition: var(--transition-fast);
        fill: var(--color-neutral-0);
        transform: scaleY(-1.0);
    }
`;

const ObfuscateWrapper = styled(Obfuscate)`
    position: relative;
    color: var(--color-text);
    text-decoration: none;
    border: 0;
    outline: 0;
`;

const ContactBoxPhone = () => {
    return (
        <HoverBox height={'20rem'} center primaryColor>
            <ObfuscateWrapper tel="+310630948453"
            linkText="phone">
                <ContactBoxItem>
                    <SvgWrapper extrasmall>
                        <ContactBoxIconPhone />
                    </SvgWrapper>
                    <Heading level={3}>Call me</Heading>
                </ContactBoxItem>
            </ObfuscateWrapper>
        </HoverBox>
    )
}

const ContactBoxMail = () => {
    return (
        <HoverBox height={'20rem'} center>
            <ObfuscateWrapper
                email="contact@jan-wagenaar.nl"
                linkText="mail">
                <ContactBoxItem>
                    <SvgWrapper extrasmall>
                        <ContactBoxIconMail />
                    </SvgWrapper>
                    <Heading level={3}>Send a message</Heading>
                </ContactBoxItem>
            </ObfuscateWrapper>
        </HoverBox>
    )
}

const ContactPosts = () => {
    return (
        <LayoutContainer>
            <LayoutRow reverse>
                <LayoutItemWrapper center>
                    <LayoutItem maxWidth>
                        <Heading level={3}>Let's get a cup of coffee</Heading>
                        <Text>If you want to get to know me better before contacting, please click below.</Text>
                        <ButtonLink to="/about">About me</ButtonLink>
                    </LayoutItem>   
                </LayoutItemWrapper>
                <LayoutItemWrapper center>
                    <SvgWrapper medium>
                        <SVGContactCoffee />
                    </SvgWrapper>
                </LayoutItemWrapper>
            </LayoutRow>
        </LayoutContainer>
    )
};

export { ContactHero, ContactPosts }