import styled from "styled-components";
import { LayoutItemWrapper } from "./layout";
import breakpoints from './breakpoints';

const HoverBox = styled(LayoutItemWrapper)`
    position: relative;

    ${breakpoints.md` 
    transition: all var(--transition-base);

    :first-child::after {
        content: "o";
        position: absolute;
        display: inline-block;
        z-index: 3;
        top: 43%;
        right: 0.75rem;
        font-size: var(--font-size-xxl);
        font-weight: bold;
    }

    :last-child::before {
        content: "r";
        position: absolute;
        display: inline-block;
        z-index: 3;
        top: 43%;
        left: 0.75rem;
        font-size: var(--font-size-xxl);
        font-weight: bold;
    }

    :first-child:hover {
        padding-right: var(--spacing-l);
    }

    :last-child:hover {
        padding-left: var(--spacing-l);
    } 
    `}
`;



export default HoverBox;